import { Component, Injector, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { BaseForm } from 'src/app/class/base.form';
import { environment } from 'src/environments/environment';
import { URLz } from 'src/app/enums/url.enum';
import { Observable } from 'rxjs';
import Swal from 'sweetalert2';
import { HttpErrorResponse } from '@angular/common/http';
import { FormHelperService } from 'src/app/service/form-helper.service';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './deposit-reconciliation.component.html',
  styleUrls: ['./deposit-reconciliation.component.css']
})
export class DepositReconciliationComponent extends BaseForm implements OnInit {

  public idList = [];
  public receipt_status = '';
  public displayedColumns = ['S No', 'Select', 'Receipt No', 'Receipt Date' , 'Amount','Deposit Banks' ,'Deposit Date','Deposit Slip No'];
  public dataSource = new MatTableDataSource();
  public multiSelect: boolean = false;
  public selectAllList : Array<any | number> =[];
  public showTable = false;

  constructor(
    public injector : Injector,
    private hepler : FormHelperService
  ) {
    super(injector);
   }

  ngOnInit(): void {
    this._component = "Direct Deposit Verification";
    this.initForm();
  }

  initForm() {
    this._fs._form = this._fb.group({
      details: this._fb.array([]),
    });
  }

  getStoreFilter(event : any){
    if(event !== '' && event !== undefined && event !== null){
      this.receipt_status = event.status;

      this._http.get({
        url: environment.TRANSACTION,
        endpoint: URLz.DIRECT_DEPOSIT_RECONCIL,
        query: {
          from_date:event.selected.startDate,
          to_date: event.selected.endDate,
          bg:event.bg,
          le:event.le,
          ou:event.ou,
          su:event.su,
          username:event.username,
          bank_account:event.bank,
          depositSlipNumber:event.deposit_slip,
          receipt_status:event.status,
        }
      }).subscribe({
        next:(res : any)=>{
          this.dataSource.data = res.data.records;
          this.selectAllList = res.data.records;
          res?.data?.records.length > 0 ? (this.showTable = true) : (
            this.showTable = false,
            this._swl.noDataFound()
          );
        },
        error:(httpErrorResponse : HttpErrorResponse)=>{
              this._vs._error_server(httpErrorResponse);
              this.showTable = false;
        }
      })
    }
  }

  selectAll(){
    var selectedPerArr: FormArray = this._fs._form.get('details') as FormArray;
    this.multiSelect = !this.multiSelect;
    if(this.multiSelect){
      if(this.selectAllList.length != 0){
        this.selectAllList.forEach(val => {
          selectedPerArr.push(
            new FormGroup({
              id: new FormControl(val.id),
            })
          );

        });
      }
    }else{
      while (0 !== selectedPerArr.length) {
        selectedPerArr.removeAt(0);
      }
    }
  }

  getcheckBoxValue(val , id){
    const selectedIdArr: FormArray = this._fs._form.get('details') as FormArray;
    if (val.target.checked) {
      selectedIdArr.push(
        new FormGroup({
          id: new FormControl(id),
        })
      );
    } else {
      let i: number = 0;
      selectedIdArr.controls.forEach((ctrl: FormGroup) => {
        if (ctrl.value.id == id) {
          selectedIdArr.removeAt(i);
          return;
        }
        i++;
      });
    }
  }

  _onSubmit() {
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    if (this._fs._form.valid || true) {
      let modify: Observable<any>;
      modify = this._http.create({body: this._fs._form.value ,url: environment.TRANSACTION ,endpoint: URLz.DIRECT_DEPOSIT_RECONCIL});
      modify.subscribe({
        next: (res: any) => {
            Swal.fire({
              title: this._translate.instant('processed') ,
              text: this._translate.instant(res.message),
              icon: 'success',
              confirmButtonColor: '#3085d6',
              confirmButtonText: '<i class="fas fa-thumbs-up"></i>',
            }).then((res) => {
              this._fs._form.reset()
              this._fhs.reloadComponent();
            });
        },
        error: (httpErrorResponse: HttpErrorResponse) => {
            this._vs._error_server(httpErrorResponse.error);
        }
      });
      this._vs._submitted = false;
    } else {
      return this._fs._form.valid;
    }
  }


}
