import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';

import { DepositReconciliationComponent } from './deposit-reconciliation.component';

const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'direct_deposit_reconcil',
        pathMatch: 'full'
      },
      {
        path: "direct_deposit_reconcil",
        component: DepositReconciliationComponent,
        data: {
          title: "Direct Deposit Reconcil",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Direct Deposit Reconcil"},
            { title: "Add" },
          ]
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DepositReconciliationRoutingModule { }
